<template>
  <div class="page page-trans-in-balance-from-wallet">
    <van-cell-group>
      <van-cell title="VipPay钱包余额">
        <van-field
          :value="$root.userInfo.balance | amountFormat"
          readonly
          colon
          input-align="right"
        >
          <template #extra> V币 </template>
        </van-field>
      </van-cell>
      <van-cell title="转入前商家余额">
        <van-field
          :value="$root.cryptoMerchantInfo.businessBalance | amountFormat"
          readonly
          colon
          input-align="right"
        >
          <template #extra> V币 </template>
        </van-field>
      </van-cell>
      <van-cell title="转入金额">
        <van-field
          v-model="amount"
          placeholder="请输入转入金额"
          colon
          input-align="right"
          :formatter="amountFormat"
        >
          <van-button
            slot="button"
            type="danger"
            size="mini"
            @click="amount = parseFloat($root.userInfo.balance).toFixed(2)"
          >
            全部
          </van-button>
        </van-field>
      </van-cell>
      <van-cell title="转入后商家余额">
        <van-field :value="businessBalance | amountFormat" readonly colon input-align="right">
          <template #extra> V币 </template>
        </van-field>
      </van-cell>
    </van-cell-group>
    <CryptoTradingAnnouncement />
    <van-button class="primary block" @click="businessDeposit"> 确定转入 </van-button>
  </div>
</template>

<script>
import CryptoTradingAnnouncement from "@/components/CryptoTradingAnnouncement.vue";
import { verifySecuretedCode } from "@/vendor/globalVerify";
import { businessDeposit } from "@/apis/crypto";
export default {
  components: {
    CryptoTradingAnnouncement,
  },
  data() {
    return {
      amount: "",
    };
  },
  computed: {
    //  转入后商家余额
    businessBalance() {
      return Number(this.$root.cryptoMerchantInfo.businessBalance) + Number(this.amount);
    },
  },
  methods: {
    // amount 格式化
    amountFormat(v) {
      console.log(v);
      if (Number(v) > Number(this.$root.userInfo.balance)) {
        return parseFloat(this.$root.userInfo.balance).toFixed(2);
      }
      return v || "";
    },
    //  钱包转入
    async businessDeposit() {
      // 验证金额
      if (!this.amount) {
        this.$toast("请输入转入金额");
        return;
      }
      // 验证安全码
      const securePassword = await verifySecuretedCode(this);
      const { ok, msg } = await businessDeposit({
        paymentType: undefined,
        amount: parseFloat(this.amount).toFixed(2),
        passageType: "BusinessTransferVippayIN",
        receivingAddress: undefined,
        securePassword,
      });

      if (ok) {
        this.$toast("转入成功");
      } else {
        this.$toast(msg);
      }
    },
  },
};
</script>

